.techs {
  width: 100%;
  background-color: rgba(39, 39, 39, 1);
}

.techs__container {
  max-width: 1280px;
  min-width: 320px;
  margin: 102px auto 0;
  padding: 0 70px;
  box-sizing: border-box;
}

.techs-info {
  display: flex;
  flex-direction: column;
  align-items: center;

}

.techs-info__description {
  font-weight: 400;
  font-size: 50px;
  line-height: 58px;
  margin: 0;
  padding: 89px 0 26px;
  letter-spacing: -2px;

}

.techs-info__paragraph {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  max-width: 460px;
  margin: 0;
  letter-spacing: -0.56px;
  text-align: center;

}

.techs__icon {
  display: flex;
  gap: 10px;
  list-style-type: none;
  margin: 0;
  padding: 100px 0;
}

.techs__icon-spisok {
  min-width: 90px;
  width: 100%;
  height: 60px;
  border-radius: 10px;
  background: #303030;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.1s ease;
}

.techs__icon-spisok:hover {
  transform: scale(1.05);
  opacity: 0.7;

}

.techs__icon-text {
  text-decoration: none;
  color: #fff;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: -0.56px;

}

@media screen and (max-width: 800px) {
  .techs__icon {
    padding: 86px 10px;
  }

  .techs__container {
    margin-top: 91px;
    padding: 0 50px;
  }

  .techs-info__description {
    padding: 80px 0 22px;
  }

  .techs-info__paragraph {
    max-width: 440px;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: -0.48px;
  }

  .techs__icon-text {
    font-size: 12px;
    letter-spacing: -0.48px;
  }

  .techs__icon-spisok {
    min-width: 84px;
    height: 57px;
  }
}

@media screen and (max-width: 680px) {
  .techs__container {
    margin-top: 51px;
  }

  .techs-info__description {
    font-size: 30px;
    line-height: normal;
    letter-spacing: -1.2px;
    padding: 55px 0 24px;
  }

  .techs-info__paragraph {
    font-size: 11px;
    line-height: 16px;
    letter-spacing: -0.44px;
  }

  .techs__icon {
    display: grid;
    justify-content: center;
    grid-template-columns: 48% 48%;
    padding: 50px 10px 90px;
  }
}

@media screen and (max-width: 400px) {
  .techs__container {
    padding: 0 18px;
  }
}