.popup {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    background: rgba(0, 0, 0, 0.5);
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.5s, opacity 0.5s linear;
}

.popup:hover {
    cursor: pointer;
}

.popup_is-opened {
    display: flex;
    visibility: visible;
    opacity: 1;
}

.popup__form {
    position: relative;
    width: 430px;
    min-height: 181px;
    margin: 195px auto auto auto;
    padding-left: 36px;
    padding-right: 36px;
    padding-top: 34px;
    padding-bottom: 37px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    z-index: 2;
    opacity: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.popup__form:hover {
    cursor: auto;
}

.popup__success-image {
    align-self: center;
    width: 120px;
    height: 120px;
    margin: 26px 0 32px 0;
}

@media screen and (max-width: 550px) {
    .popup__form {
        width: 282px;
        margin-top: 123px;
        padding-top: 25px;
        padding-left: 22px;
        padding-right: 22px;
        padding-bottom: 25px;
    }
}

.popup__close-button {
    position: absolute;
    top: -40px;
    right: -40px;
    background-color: transparent;
    background-image: url('../../image/btn-close.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 32px, 32px;
    width: 32px;
    height: 32px;
    border: none;
}

.popup__close-button:hover {
    opacity: 0.6;
    cursor: pointer;
}


.popup__title {
    font-weight: 900;
    font-size: 24px;
    line-height: 1;
    margin: 0 0 11px 0;
    padding: 0;
    color: black;
}


.popup__title_centered {
    text-align: center;
}

@media screen and (max-width: 550px) {
    .popup__title {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 40px;
    }

    .popup__close-button {
        top: -36px;
        right: 0;
        background-size: 20px, 20px;
        width: 20px;
        height: 20px;
    }
}